<template>
	<div>
		<div>
			<div style="display: flex; justify-content: space-between;">
				<div style="display: flex; justify-content: flex-start;">
					<div>
						搜关键字：
						<a-input style="width:200px; margin: 20px 0 ;" v-model="searchParams.key" placeholder="用户名/邮编" />
					</div>
					<div style="margin: 20px 0 0 25px">
						性别：
						<a-select v-model="searchParams.gender" style="width: 150px;" placeholder="选择性别" :allowClear="true">
							<a-select-option value="M">男</a-select-option>
							<a-select-option value="F">女</a-select-option>
						</a-select>
					</div>
					<a-button type="primary" icon="search" style="margin: 20px 0 0 50px" @click="search">
						搜索用户
					</a-button>
				</div>
				<a-button type="danger" icon="edit" style="margin-top: 25px" @click="showAddWindow">
					发布公告
				</a-button>
			</div>
			<div v-show="!collapsed" style="display: flex; justify-content: flex-start;">
				<div>
					职业类型：
					<a-select v-model="searchParams.occupation" style="width: 200px;" placeholder="选择职业" :allowClear="true">
						<a-select-option :value=key v-for="(value, key) in occupationList">
							{{value}}
						</a-select-option>
					</a-select>
				</div>
				<div style="margin-left: 25px;">
					年龄：
					<a-select v-model="searchParams.age" style="width: 150px;" placeholder="选择年龄段" :allowClear="true">
						<a-select-option :value=key v-for="(value, key) in ageList">
							{{value}}
						</a-select-option>
					</a-select>
				</div>
			</div>
			<a-divider style="color: #999999; margin-top: 10px;">
				<a-icon @click="collapsed=!collapsed" :type="collapsed?'down-circle':'up-circle'" />
				{{collapsed?"展开":"收起"}}更多选项
			</a-divider>
		</div>
		
		<a-modal v-model="add" title="发布公告" @ok="addBulletin" :afterClose="cancel">
			<p>编辑：{{userInfo.name}}</p>
			<p>请输入公告：</p>
			<a-input type="textarea" v-model="addParams.content" />
		</a-modal>
		
		<a-table :columns="columns" :data-source="dataSource" :loading="dataloading" :pagination=" {pageSize:pageSize}"
		 :row-selection="rowSelection" :scroll="{ y: 280 }">
			<b slot="name" slot-scope="text">{{ text }}</b>
			<span slot="gender" slot-scope="text">{{ text=="M"?"男":"女" }}</span>
			<span slot="age" slot-scope="text" v-text="ageList[text]"></span>
			<span slot="occupation" slot-scope="text" v-text="occupationList[text]"></span>
		</a-table>
	</div>

</template>

<script>
	import {
		mapMutations,
		mapState
	} from "vuex";
	const columns = [{
			title: '用户名',
			dataIndex: 'name',
			scopedSlots: {
				customRender: 'name'
			},
		},
		{
			title: '性别',
			dataIndex: 'gender',
			scopedSlots: {
				customRender: 'gender'
			},
		},

		{
			title: '年龄',
			dataIndex: 'age',
			scopedSlots: {
				customRender: 'age'
			},
		},
		{
			title: '职业',
			dataIndex: 'occupation',
			scopedSlots: {
				customRender: 'occupation'
			},
		},
		{
			title: '邮编',
			dataIndex: 'zipCode',
		},
	];
	columns.forEach((r) => {
		r.align = 'center'
	});

	export default {
		data() {
			return {
				searchParams: {
					key: null,
					age: null,
					occupation: null,
					gender: null,
				},
				addParams: {
					adminId: 0,
					userIds: [],
					content: null,
				},
				add: false,
				dataSource: null,
				page: 1,
				pageSize: 10,
				dataloading: true,
				columns,
				collapsed: true,
				ageList: {
					1: "Under 18",
					18: "18-24",
					25: "25-34",
					35: "35-44",
					45: "45-49",
					50: "50-55",
					56: "56+"
				},
				occupationList: ['other', 'academic/educator', 'artist', 'clerical/admin', 'college/grad student',
					'customer service', 'doctor/health care', 'executive/managerial', 'farmer', 'homemaker', 'K-12 student',
					'lawyer', 'programmer', 'retired', 'sales/marketing', 'scientist', 'self-employed',
					'technician/engineer', 'tradesman/craftsman', 'unemployed', 'writer'
				],
			};
		},
		computed: {
			...mapState(['requestPath', 'userInfo']),
			rowSelection() {
				return {
					onSelect: (record, selected, selectedRows) => {
						this.addParams.userIds = []
						for (var i = 0; i < selectedRows.length; i++) {
							this.addParams.userIds.push(selectedRows[i].uid);
						}
					},
					onSelectAll: (selected, selectedRows, changeRows) => {
						this.addParams.userIds = []
						for (var i = 0; i < selectedRows.length; i++) {
							this.addParams.userIds.push(selectedRows[i].uid);
						}
					},
				}
			},
		},
		async created() {
			this.getAllAge();
			this.getAllOccupation();
			this.getAllUser();
		},
		methods: {
			async getAllAge() {
				if (this.$store.state.userInfo) {
					let {
						data
					} = await this.$get(this.$store.state.requestPath + 'allAge');
					this.ageList = data.data;
				} else {
					console.log("userInfo not found");
				}
			},
			async getAllOccupation() {
				if (this.$store.state.userInfo) {
					let {
						data
					} = await this.$get(this.$store.state.requestPath + 'allOccupation');
					this.occupationList = data.data;
				} else {
					console.log("userInfo not found");
				}
			},
			async getAllUser() {
				this.dataloading = true;
				if (this.$store.state.userInfo) {
					let {
						data
					} = await this.$get(this.$store.state.requestPath + 'allUser');
					this.dataSource = data.data;
					this.dataloading = false;
				} else {
					console.log("userInfo not found");
				}
			},
			async addBulletin() {
				if (this.addParams.userIds.length==0){
					this.$message.warning('请选择公告接收方');
					this.add = false;
					return;
				}
				this.addParams.adminId = this.userInfo.uid;
				let {
					data
				} = await this.axios.post(this.requestPath + '/bulletin/add', this.addParams);
				if (data.code == 0) {
					this.$message.success('发布成功');
					this.add = false;
				} else {
					this.$message.error('发布失败');
				}
			},
			async search() {
				this.dataloading = true;
				let {
					data
				} = await this.axios.post(this.requestPath + 'searchUser', this.searchParams);
				this.dataSource = data.data;
				this.dataloading = false;
			},
			cancel(e) {
				console.log(e);
			},
			showAddWindow() {
				this.add = true;
			},
		},
	};
</script>

<style lang="scss">
</style>
